import React from 'react';
import { LoadingSpinner, PlaceholderText, ImageIcon } from 'components/atoms';
import { getUserFullName } from 'utils/helpers';


const TeacherClassStudents = ({ classStudentsLoading, classStudents }) => {
  return (
    <div className="page-content-block container">
      <div className="students">
        <h4 className="block-title text-right"><ImageIcon name="students" width="60px" />طلاب الصف</h4>

        <div className="students-container">
          <div className="row">
            {classStudentsLoading ? (
              <LoadingSpinner large />
            ) : null}

            {classStudents.length === 0 ? (
              <PlaceholderText text='لا يوجد طلاب منضمين إلى هذا الصف حتى الآن!' />
            ) : (
              <table className="table">
                <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">اسم الطالب</th>
                  <th scope="col">تاريخ الانضمام لمفكرة الصف</th>
                </tr>
                </thead>
                <tbody>

                {classStudents.map((student, index) => (
                  <tr key={`${student.user['first_name']}_${index}`}>
                    <th scope="row">{index + 1}</th>
                    <td>{getUserFullName(student.user)}</td>
                    <td>{student['enrolled_in_date'].slice(0, 10)}</td>
                  </tr>
                ))}
                </tbody>
              </table>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeacherClassStudents;
